import React from 'react';
import styled, { css } from 'styled-components';
import { FormulaIcon } from './icon';

const FormulaPlaceholderWrapper = styled.div<Pick<Props, 'notAllowed' | 'isSelected' | 'isEditing' | 'readOnly'>>`
  position: relative;
  height: 32px;
  padding: 0 4px;
  border-radius: 4px;
  transition: .3s ease;
  user-select: none;
  display: flex;
  align-items: center;
  justify-content: center;

  ${({ readOnly }) => (readOnly ? undefined : css`
    width: 96px;
  `)}

  ${({ readOnly }) => (readOnly ? undefined : css`
    &:hover {
      background-color: rgba(17, 31, 44, 0.08);
    }

    &:active {
      background-color: rgba(17, 31, 44, 0.08);
    }
  `)}

  ${({ isEditing }) => (isEditing ? css`
    background-color: rgba(126, 134, 142, 0.16);
  ` : undefined)}

  ${({ isSelected }) => (isSelected ? css`
    background-color: rgba(0, 106, 254, 0.12);
  ` : undefined)}

  cursor: ${({ notAllowed }) => (notAllowed ? 'not-allowed' : 'pointer') };
`;

const IconWrapper = styled.div`
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 2px;
  color: rgba(23, 26, 29, 0.6);
`;

const Placeholder = styled.div`
  height: 100%;
  font-size: 14px;
  line-height: 21px;
  display: flex;
  align-items: center;

  & > span {
    color: rgba(23, 26, 29, 0.4);
    font-size: 14px;
    line-height: 21px;
    cursor: inherit;
  }
`;

interface Props {
  notAllowed: boolean;
  locale: Record<string, any>;
  isSelected?: boolean;
  isEditing?: boolean;
  readOnly?: boolean;
}

export default (props: Props) => {
  const { notAllowed, locale, isSelected, isEditing, readOnly } = props;
  return (
    <FormulaPlaceholderWrapper
      notAllowed={notAllowed}
      isSelected={isSelected && !isEditing}
      isEditing={isEditing}
      readOnly={readOnly}
    >
      <Placeholder>
        <IconWrapper>
          <FormulaIcon />
        </IconWrapper>
        <span>
          {locale.placeholder}
        </span>
      </Placeholder>
    </FormulaPlaceholderWrapper>
  );
};
